<template><section><html><head></head><body><h1>BillboardCollection</h1>
<p><code v-pre="">vc-collection-primitive-billboard</code> 组件用于添加布告板图元集合，布告板是场景中与视口对齐的图像。</p>
<h2>示例</h2>
<h3>加载布告板图元集合</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-primitive-billboard ref="billboardCollection" :billboards="billboards" @click="clicked" @mouseout="mouseout" @mouseover="mouseover"></vc-collection-primitive-billboard>
        <vc-collection-primitive-billboard>
          <vc-primitive-billboard @click="clicked" @mouseout="mouseout" @mouseover="mouseover" :image="image" :scale="scale" :show="show" :distance-display-condition="distanceDisplayCondition" :horizontal-origin="horizontalOrigin" :position="position"></vc-primitive-billboard>
        </vc-collection-primitive-billboard>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
