        <span class="hljs-keyword">if</span> (e.<span class="hljs-property">cesiumObject</span> <span class="hljs-keyword">instanceof</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Billboard</span>) {
          <span class="hljs-variable language_">this</span>.<span class="hljs-property">scale</span> = <span class="hljs-number">0.25</span> <span class="hljs-comment">// or e.cesiumObject.scale = 0.25</span>
